import React, { Component } from "react";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import useForm from "./useForms";
import axios from "axios";
import { withRouter } from "react-router";
import validate from "./FormValidation";
import auth from "./../../utils/auth";
import Fab from "@material-ui/core/Fab";
import DeleteIcon from "@material-ui/icons/Delete";
class ExistingAddressComponent extends React.Component {
  state = {
    line1: "",
    line2: "",
    city: "",
    country: "",
    postcode: ""
  };
  componentDidMount() {
    console.log(
      "Inside component Did Mount For Edit/Ceate Patient",
      this.props.data
    );
    this.setState(this.props.data);
  }
  onDelete = e => {
    e.preventDefault();

    this.props.onClick(this.props.index);
  };
  updateAddress = data => {
    console.log("Inside update Existting Address", data);
    this.props.onUpdate(this.props.index, data);
  };
  render() {
    return (
      <div id="addresscomponent">
        <Grid container spacing={1}>
          <Grid item xs={2}>
            <TextField
              autoComplete="fname"
              name="line1"
              variant="outlined"
              required
              fullWidth
              id="line1"
              label="Line 1"
              autoFocus
              value={this.state.line1}
              onChange={e => {
                this.setState({ line1: e.target.value });
                this.updateAddress(this.state);
              }}
            />
          </Grid>
          <Grid item xs={2}>
            <TextField
              variant="outlined"
              required
              fullWidth
              id="line2"
              label="Line 2"
              name="line1"
              autoComplete="line2"
              value={this.state.line2}
              onChange={e => {
                this.setState({ line2: e.target.value });
                this.updateAddress(this.state);
              }}
            />
          </Grid>
          <Grid item xs={2}>
            <TextField
              variant="outlined"
              required
              fullWidth
              id="city"
              label="City"
              name="city"
              autoComplete="city"
              value={this.state.city}
              onChange={e => {
                this.setState({ city: e.target.value });
                this.updateAddress(this.state);
              }}
            />
          </Grid>
          <Grid item xs={2}>
            <TextField
              variant="outlined"
              required
              fullWidth
              id="country"
              label="Country"
              name="country"
              autoComplete="country"
              value={this.state.country}
              onChange={e => {
                this.setState({ country: e.target.value });
                this.updateAddress(this.state);
              }}
            />
          </Grid>
          <Grid item xs={2}>
            <TextField
              variant="outlined"
              required
              fullWidth
              id="postcode"
              label="Postal Code"
              name="postcode"
              autoComplete="postcode"
              value={this.state.postcode}
              onChange={e => {
                this.setState({ postcode: e.target.value });
                this.updateAddress(this.state);
              }}
            />
          </Grid>
          <Fab color="primary" aria-label="delete">
            <DeleteIcon onClick={this.onDelete} />
          </Fab>
          {/* <button id="deleteAddress" onClick={this.onDelete}>
            Delete Address
          </button> */}
        </Grid>
      </div>
    );
  }
}
export default ExistingAddressComponent;
