import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import NavigateNextIcon from "@material-ui/icons/ArrowForwardIos";
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos";
import Grid from "@material-ui/core/Grid";
import Divider from "@material-ui/core/Divider";
import props from "./../chatbotOptions/chatbotOptions";

const useStyles = makeStyles(theme => ({
  root: {
    width: "fit-content",
    border: `1px solid ${theme.palette.divider}`,
    borderRadius: theme.shape.borderRadius,
    backgroundColor: theme.palette.background.default,
    color: theme.palette.text.primary,
    "& svg": {
      margin: theme.spacing(2)
    },
    "& hr": {
      margin: theme.spacing(0, 0.5)
    }
  }
}));

export default function VerticalDividers(props) {
  const classes = useStyles();

  return (
    <Grid container alignItems="center" className={classes.root}>
      Previous Page
      <ArrowBackIosIcon onClick={props.onPrev} />
      <NavigateNextIcon onClick={props.onNext} />
      Next Page
      <Divider orientation="vertical" />
    </Grid>
  );
}
