import React, { Component, useEffect } from "react";
import { ButtonToolbar, Button } from "react-bootstrap";
import MaterialTable from "material-table";
import logo from "assets/img/reactlogo.png";
import Sidebar from "components/Sidebar/Sidebar.jsx";
import FixedPlugin from "components/FixedPlugin/FixedPlugin.jsx";
import routes from "routes.js";
import image from "assets/img/sidebar-2.jpg";
import dashboardStyle from "assets/jss/material-dashboard-react/layouts/dashboardStyle.jsx";
import Navbar from "components/Navbars/Navbar.jsx";
import "perfect-scrollbar/css/perfect-scrollbar.css";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import axios from "axios";
import UserModal from "./userCreationModal";

import auth from "./../../utils/auth";
class userTable extends Component {
  state = {
    columns: [
      { title: "First Name", field: "firstName" },
      { title: "Last  Name", field: "lastName" },
      { title: "Phone Number", field: "phoneNumber" }
    ],
    data: [],
    image: image,
    color: "blue",
    hasImage: true,
    fixedClasses: "dropdown show",
    mobileOpen: false,
    showModal: false,
    mode: "",
    modalData: ""
  };

  async componentDidMount() {
    console.log("User List Component", this.state.data);
    console.log(auth.getURL());
    var self = this;
    const token = auth.getToken();
    const config = {
      headers: { Authorization: `Bearer ${token}` }
    };
    const { data } = await axios.get(`${auth.getURL()}/api/v1/patients`, config);

    if (data) {
      console.log(data);

      self.setState({ data: data });
    }
    console.log("state", this.state);
  }
  async rowAdded() {
    this.setState({ ...this.setState, showModal: false });
    var self = this;
    const token = auth.getToken();
    const config = {
      headers: { Authorization: `Bearer ${token}` }
    };
    console.log(auth.getURL());
    const { data } = await axios.get(`${auth.getURL()}/api/v1/patients`, config);

    if (data) {
      console.log(data);

      self.setState({ data: data });
    }
  }
  async rowEdited() {
    this.setState({ ...this.setState, showModal: false });
    var self = this;
    const token = auth.getToken();
    const config = {
      headers: { Authorization: `Bearer ${token}` }
    };
    const { data } = await axios.get(`${auth.getURL()}/api/v1/patients`, config);

    if (data) {
      console.log(data);

      self.setState({ data: data });
    }
  }
  onClose = () => {
    this.setState({ ...this.setState, showModal: false });
  };
  onDeleteRow = async rowData => {
    console.log("Inside dete Row", rowData);
    const url = `${auth.getURL()}/api/v1/patient/${rowData.id}`;
    var self = this;
    const token = auth.getToken();
    const config = {
      headers: { Authorization: `Bearer ${token}` }
    };
    const { data } = await axios.delete(url, config);
    if (data) {
      console.log(data);

      this.rowEdited();
    }
  };
  onRowRemoved = rowData => {
    console.log("remove Row", rowData);
    var data = this.state.data;
    console.log("Before Remove Data", data);
    if (this.state.data.length > 1) {
      for (var i = 0; i < data.length; i++) {
        console.log("data", data[i]);
        if (data[i].id === rowData.id) {
          this.state.data.splice(i, 1);
          var updatedData = this.state.data;
          break;
        }
      }
    } else {
      var updatedData = [];
    }
    console.log("updated data", updatedData);
    this.setState({
      data: updatedData
    });
  };

  async row(oldData) {
    console.log("old data", oldData);
  }
  handleImageClick = image => {
    this.setState({ image: image });
  };
  handleColorClick = color => {
    this.setState({ color: color });
  };
  handleFixedClick = () => {
    if (this.state.fixedClasses === "dropdown") {
      this.setState({ fixedClasses: "dropdown show" });
    } else {
      this.setState({ fixedClasses: "dropdown" });
    }
  };
  mainPanel = React.createRef();
  render() {
    console.log("Props--userlist->", this.props);
    console.log("token", auth.getToken());
    const { classes, ...rest } = this.props;

    return (
      <div className={classes.wrapper}>
        <Sidebar
          routes={routes}
          logoText={"Zorang"}
          logo={logo}
          image={this.state.image}
          handleDrawerToggle={this.handleDrawerToggle}
          open={this.state.mobileOpen}
          color={this.state.color}
          {...rest}
        />

        <div className={classes.mainPanel} ref={this.mainPanel}>
          <div className={classes.content}>
            <div className={classes.container}>
              <Navbar
                routes={routes}
                handleDrawerToggle={this.handleDrawerToggle}
                {...rest}
              />
              <MaterialTable
                title="Patients"
                columns={this.state.columns}
                data={this.state.data}
                disableUnderline="true"
                actions={[
                  {
                    icon: "add",
                    tooltip: "Add Patient",
                    isFreeAction: true,
                    onClick: event =>
                      this.setState({
                        ...this.state,
                        showModal: true,
                        mode: "Add",
                        modalData: []
                      })
                  },
                  {
                    icon: "edit",
                    tooltip: "Edit/View Patient",

                    onClick: (event, rowData) =>
                      this.setState({
                        ...this.state,
                        showModal: true,
                        mode: "Edit",
                        modalData: rowData
                      })
                  },
                  {
                    icon: "delete",
                    tooltip: "Delete Patient",
                    onClick: (event, rowData) => {
                      if (window.confirm("Delete this patient?")) {
                        this.onDeleteRow(rowData);
                      }
                    }
                  },
                  {
                    icon: "clear",
                    tooltip: "Remove Patient from List",

                    onClick: (event, rowData) => this.onRowRemoved(rowData)
                  }
                ]}
                // editable={{
                //   onRowUpdate: (newData, oldData) =>
                //     new Promise(resolve => {
                //       setTimeout(() => {
                //         resolve();
                //         this.rowUpdated(newData);
                //         const data = [...this.state.data];
                //         data[data.indexOf(oldData)] = newData;
                //         this.setState({ ...this.state, data });
                //       }, 600);
                //     }),
                //   onRowDelete: oldData =>
                //     new Promise(resolve => {
                //       setTimeout(() => {
                //         resolve();
                //         const data = [...this.state.data];
                //         data.splice(data.indexOf(oldData), 1);
                //         this.setState({ ...this.state, data });
                //         this.rowDelete(oldData);
                //       }, 600);
                //     })
                // }}
              />
              <UserModal
                show={this.state.showModal}
                onAdd={this.rowAdded.bind(this)}
                onHide={this.onClose}
                onEdit={this.rowEdited.bind(this)}
                data={this.state.modalData}
                mode={this.state.mode}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default withStyles(dashboardStyle)(userTable);
