import React, { Component } from "react";
import "./table.css";

class Table extends Component {
  constructor(props) {
    super();
    console.log(props);
  }
  state = {};
  render() {
    return (
      <table>
        <thead>
          <tr>
            <td colspan="2">{this.props.documentData["fileName"]}</td>
          </tr>
          <tr>
            <th>Entities</th>
            <th>Value</th>
          </tr>
        </thead>
        <tbody>
          {Object.keys(this.props.documentData).map(entity => {
            if (entity === "fileName" || entity === "text") {
              return;
            }
            return (
              <tr>
                <td>{entity.toUpperCase()}</td>
                <td>{this.props.documentData[entity]}</td>
              </tr>
            );
          })}
        </tbody>
      </table>
    );
  }
}

export default Table;
