import React from "react";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import useForm from "./useForms";
import axios from "axios";
import { withRouter } from "react-router";
import GoogleLogin from "react-google-login";
import SocialLogin from "./socialLogin";
import validate from "./loginFormValidation";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import firebase from "./firebase.js";
import { async } from "q";
function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {"Copyright © "}
      <Link color="inherit" href="https://material-ui.com/">
        Your Website
      </Link>{" "}
      {new Date().getFullYear()}
      {"."}
    </Typography>
  );
}

const useStyles = makeStyles(theme => ({
  "@global": {
    body: {
      backgroundColor: theme.palette.common.white
    }
  },
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center"
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: "#fd7e14"
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1)
  },
  BtnGoogle: {
    margin: " 2px",
    width: "380px",
    height: "35px",
    borderRadius: "4px;,  background: #3f51b5",
    color: "white",
    border: " 0px transparent",
    textAlign: "center"
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
    backgroundColor: "#36cce6",
    color: "white",
    "&:hover": {
      backgroundColor: "#26c8d0",
      // Reset on mouse devices
      "@media (hover: none)": {
        backgroundColor: "  #36cce6"
      }
    }
  }
}));
function SignIn(props) {
  const classes = useStyles();
  console.log("props", props);
  const { values, handleChange, handleSubmit, errors } = useForm(
    loginUser,
    validate
  );
  const [open, setOpen] = React.useState(false);
  const [text, setText] = React.useState("");
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  //console.log("props", props);
  async function loginUser() {
    console.log("Props-------", props);
    console.log(values);
    const { email, password, remember } = values;
    console.log("before login");
    try {
      await firebase
        .auth()
        .signInWithEmailAndPassword(email, password)
        .then(user => {
          props.auth.setUserInfo(user, remember);
        });
    } catch (error) {
      alert(error);
    }
    const idToken = await firebase.auth().currentUser.getIdToken();
    props.auth.setToken(idToken, remember);
    // console.log("idToken", idToken);
    // alert("Token" + idToken);
    // window.location = "/admin";
    props.history.push("/admin");
  }
  async function onGoogle() {
    const { email, password, remember } = values;
    var provider = new firebase.auth.GoogleAuthProvider();
    await firebase
      .auth()
      .signInWithPopup(provider)
      .then(function(result) {
        // This gives you a Google Access Token. You can use it to access the Google API.
        var token = result.credential.accessToken;
        props.auth.setUserInfo(result.user, remember);
        props.auth.setToken(token, remember);
        // The signed-in user info.
        var user = result.user;
        props.history.push("/admin");
        // ...
      })
      .catch(function(error) {
        // Handle Errors here.
        var errorCode = error.code;
        var errorMessage = error.message;
        // The email of the user's account used.
        var email = error.email;
        // The firebase.auth.AuthCredential type that was used.
        var credential = error.credential;
        // ...
      });
  }
  const handleRegister = () => {
    props.history.push("/register");
  };
  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Sign in
        </Typography>
        <form className={classes.form} onSubmit={handleSubmit} noValidate>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="email"
            label="Email Address"
            name="email"
            autoComplete="email"
            autoFocus
            onChange={handleChange}
            value={values.email || ""}
            type="text"
            error={errors.email}
            helperText={errors.email}
          />

          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            autoComplete="current-password"
            onChange={handleChange}
            value={values.password}
            error={errors.password}
            helperText={errors.password}
          />
          <FormControlLabel
            control={
              <Checkbox
                value={values.remember}
                onClick={handleChange}
                color="primary"
              />
            }
            // onClick={handleChange}
            label="Remember me"
          />
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
          >
            Sign In
          </Button>
          {/* <GoogleLogin
            clientId="499483309789-7eocphv0g1s3mom1gaafrv32o9nmi6c8.apps.googleusercontent.com"
            buttonText="Sign In With Google"
            onSuccess={responseGoogle}
            onFailure={onFailure}
            cookiePolicy={"single_host_origin"}
            render={renderProps => (
              <SocialLogin clickHandler={renderProps.onClick} />
            )}
          /> */}
          <Button
            type="button"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.BtnGoogle}
            onClick={onGoogle}
          >
            <img
              width="20px"
              alt='Google "G" Logo'
              src="https://upload.wikimedia.org/wikipedia/commons/thumb/5/53/Google_%22G%22_Logo.svg/512px-Google_%22G%22_Logo.svg.png"
            />
            Google Sign In
          </Button>
          <Grid container>
            <Grid item xs></Grid>
            <Grid item>
              <Button onClick={handleRegister} variant="outlined">
                "Don't have an account? Sign Up"
              </Button>
            </Grid>
          </Grid>
        </form>
      </div>
      <Box mt={8}></Box>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{"Login Failed"}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            {text}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            OK
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
}

export default withRouter(SignIn);
