import React, { Component } from "react";
import { Document, Page } from "react-pdf";
import { pdfjs } from "react-pdf";
import NavigateNextIcon from "@material-ui/icons/ArrowForwardIos";
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos";
import ToolBar from "./documentViewerToolbar";
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;
export default class documentViewer extends Component {
  state = {
    file: { url: `http://localhost:3000/api/contracts/${this.props.fileid}` },
    numPages: null,
    pageNumber: 1
  };

  onFileChange = url => {
    this.file = { url };
  };
  onDocumentLoadSuccess = ({ numPages }) => {
    this.setState({ numPages });
  };

  goToPrevPage = () => {
    if (this.state.pageNumber - 1 > 0) {
      this.setState(state => ({ pageNumber: state.pageNumber - 1 }));
    }
  };
  goToNextPage = () => {
    if (this.state.pageNumber + 1 <= this.state.numPages) {
      this.setState(state => ({ pageNumber: state.pageNumber + 1 }));
    }
  };

  render() {
    const { pageNumber, numPages } = this.state;
    console.log("Props for pdf", this.props);
    const file = this.props.filename;
    const fileType = file.substring(file.indexOf(".") + 1, file.length);
    console.log("file Type----", fileType);
    if (fileType == "txt") {
      return (
        <p style={{ "white-space": "pre-line" }}>
          {typeof this.props.data !== "Object"
            ? this.props.data
            : this.props.data
                .toString()
                .replace(/\s{2,}/g, " ")
                .trim()}
        </p>
      );
    } else if (fileType == "pdf") {
      return (
        <div style={{ color: "black" }}>
          <ToolBar onPrev={this.goToPrevPage} onNext={this.goToNextPage} />

          <div>
            <Document
              file={this.state.file}
              onLoadSuccess={this.onDocumentLoadSuccess}
            >
              <Page pageNumber={pageNumber} />
            </Document>
          </div>

          <p>
            Page Number {pageNumber} of {numPages}
          </p>
        </div>
      );
    }
  }
}
