import React, { Component } from "react";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import Fab from "@material-ui/core/Fab";
import TextField from "@material-ui/core/TextField";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import useForm from "./useForms";
import axios from "axios";
import { withRouter } from "react-router";
import validate from "./FormValidation";
import auth from "./../../utils/auth";
import AddIcon from "@material-ui/icons/Add";
class AddressComponent extends React.Component {
  state = {
    line1: "",
    line2: "",
    city: "",
    country: "",
    postcode: "",
    fields: ["line1", "line2", "city", "country", "postcode"],
    errors: ""
  };

  addQuestion = e => {
    e.preventDefault();
    var data = {
      line1: this.state.line1,
      line2: this.state.line2,
      city: this.state.city,
      country: this.state.country,
      postcode: this.state.postcode
    };
    if (this.handleValidation()) {
      this.props.onClick(data);
      this.setState({
        line1: "",
        line2: "",
        city: "",
        country: "",
        postcode: ""
      });
    }
  };
  handleValidation() {
    let fields = this.state.fields;
    let errors = {};
    let formIsValid = true;

    //Name
    if (!fields["line1"]) {
      formIsValid = false;
      errors["line1"] = "Cannot be empty";
    }
    if (!fields["line2"]) {
      formIsValid = false;
      errors["line2"] = "Cannot be empty";
    }
    if (!fields["city"]) {
      formIsValid = false;
      errors["city"] = "Cannot be empty";
    }
    if (!fields["country"]) {
      formIsValid = false;
      errors["country"] = "Cannot be empty";
    }
    if (!fields["postcode"]) {
      formIsValid = false;
      errors["postcode"] = "Cannot be empty";
    }

    if (typeof fields["line1"] !== "undefined") {
      if (!fields["line1"].match(/^[a-zA-Z0-9_.-]*$/)) {
        formIsValid = false;
        errors["line1"] = "Name can have only letters";
      }
    }
    if (typeof fields["line2"] !== "undefined") {
      if (!fields["line2"].match(/^[a-zA-Z0-9_.-]*$/)) {
        formIsValid = false;
        errors["line2"] = "Name can have only letters";
      }
    }
    if (typeof fields["city"] !== "undefined") {
      if (!fields["city"].match(/^[a-zA-Z0-9_.-]*$/)) {
        formIsValid = false;
        errors["city"] = "Name can have only letters";
      }
    }
    if (typeof fields["postcode"] !== "undefined") {
      if (!fields["postcode"].match(/^[0-9\b]+$/)) {
        formIsValid = false;
        errors["postcode"] = "Name can have only numbers";
      }
    }

    this.setState({ errors: errors });
    return formIsValid;
  }

  render() {
    return (
      <div id="addresscomponent">
        <Grid container spacing={1}>
          <Grid item xs={2}>
            <TextField
              autoComplete="line1"
              name="line1"
              variant="outlined"
              required
              fullWidth
              id="line1"
              label="Line 1"
              autoFocus
              defaultValue={this.state.line1}
              onChange={e => {
                this.setState({ line1: e.target.value });
                let fields = this.state.fields;
                fields["line1"] = e.target.value;
                this.setState({ fields: fields });
              }}
            />
            <span style={{ color: "red" }}>{this.state.errors["line1"]}</span>
          </Grid>
          <Grid item xs={2}>
            <TextField
              variant="outlined"
              required
              fullWidth
              id="line2"
              label="Line 2"
              name="line1"
              autoComplete="line2"
              defaultValue={this.state.line2}
              onChange={e => {
                this.setState({ line2: e.target.value });
                let fields = this.state.fields;
                fields["line2"] = e.target.value;
                this.setState({ fields: fields });
              }}
            />
            <span style={{ color: "red" }}>{this.state.errors["line2"]}</span>
          </Grid>
          <Grid item xs={2}>
            <TextField
              variant="outlined"
              required
              fullWidth
              id="city"
              label="City"
              name="city"
              autoComplete="city"
              defaultValue={this.state.city}
              onChange={e => {
                this.setState({ city: e.target.value });
                let fields = this.state.fields;
                fields["city"] = e.target.value;
                this.setState({ fields: fields });
              }}
            />
            <span style={{ color: "red" }}>{this.state.errors["city"]}</span>
          </Grid>
          <Grid item xs={2}>
            <TextField
              variant="outlined"
              required
              defaultValue={this.state.country}
              fullWidth
              id="country"
              label="Country"
              name="country"
              autoComplete="country"
              onChange={e => {
                this.setState({ country: e.target.value });
                let fields = this.state.fields;
                fields["country"] = e.target.value;
                this.setState({ fields: fields });
              }}
            />
            <span style={{ color: "red" }}>{this.state.errors["country"]}</span>
          </Grid>
          <Grid item xs={2}>
            <TextField
              variant="outlined"
              required
              fullWidth
              id="postcode"
              label="Postal Code"
              defaultValue={this.state.postcode}
              name="postcode"
              autoComplete="postcode"
              onChange={e => {
                this.setState({ postcode: e.target.value });
                let fields = this.state.fields;
                fields["postcode"] = e.target.value;
                this.setState({ fields: fields });
              }}
            />
            <span style={{ color: "red" }}>
              {this.state.errors["postcode"]}
            </span>
          </Grid>
          <Fab color="primary" aria-label="add">
            <AddIcon onClick={this.addQuestion} />
          </Fab>
        </Grid>
      </div>
    );
  }
}
export default AddressComponent;
