import React, { Component } from "react";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import useForm from "./useForms";
import axios from "axios";
import { withRouter } from "react-router";
import validate from "./FormValidation";
import auth from "./../../utils/auth";
import AddressComponent from "./addressComponent";
import ExistingAddressComponent from "./existingAddressComponent";
class User extends Component {
  state = {
    id: "",
    firstName: "",
    lastName: "",
    phoneNumber: "",
    addressSet: [],
    fields: ["firstname", "lastName", "phoneNmber"],
    errors: ""
  };

  componentDidMount() {
    console.log(
      "Inside component Did Mount For Edit/Ceate Patient",
      this.props.data
    );
    this.setState(this.props.data);
    let fields = this.state.fields;
    fields["firstName"] = this.props.data.firstName;
    fields["lastName"] = this.props.data.lastName;
    fields["phoneNumber"] = this.props.data.phoneNumber;
    this.setState({ fields: fields });
  }

  handleValidation() {
    let fields = this.state.fields;
    let errors = {};
    let formIsValid = true;

    //Name
    if (!fields["firstName"]) {
      formIsValid = false;
      errors["firstName"] = "Cannot be empty";
    }
    if (!fields["lastName"]) {
      formIsValid = false;
      errors["lastName"] = "Cannot be empty";
    }

    if (typeof fields["firstName"] !== "undefined") {
      if (!fields["firstName"].match(/^[a-zA-Z]+$/)) {
        formIsValid = false;
        errors["firstName"] = "Name can have only letters";
      }
    }
    if (typeof fields["lastName"] !== "undefined") {
      if (!fields["lastName"].match(/^[a-zA-Z]+$/)) {
        formIsValid = false;
        errors["lastName"] = "Name can have only letters";
      }
    }

    //Email
    if (!fields["phoneNumber"]) {
      formIsValid = false;
      errors["phoneNumber"] = "Cannot be empty";
    }

    if (typeof fields["phoneNumber"] !== "undefined") {
      if (!fields["phoneNumber"].match(/^[0-9\b]+$/)) {
        formIsValid = false;
        errors["phoneNumber"] = "Email is not valid";
      }
    }

    if (this.state.addressSet.length < 1) {
      formIsValid = false;
      errors["Address"] = "Add atleast one Address";
    }

    this.setState({ errors: errors });
    return formIsValid;
  }

  addAddress = data => {
    console.log("Inside State chnaged", data);
    this.setState(prevState => ({
      addressSet: [...prevState.addressSet, data]
    }));

    console.log("After State", this.state);
  };

  deleteAddress = data => {
    console.log("Inside State chnaged", data);
    if (this.state.addressSet.length > 1) {
      var updatedaddressSet = this.state.addressSet.splice(data, 1);
    } else {
      var updatedaddressSet = [];
    }
    console.log(updatedaddressSet);
    this.setState({
      addressSet: updatedaddressSet
    });

    console.log("After State", this.state);
  };

  updateAddress = (index, data) => {
    console.log("Inside State chnaged", data);
    var updatedaddressSet = this.state.addressSet;
    updatedaddressSet[index] = data;
    console.log(updatedaddressSet);
    this.setState({
      addressSet: updatedaddressSet
    });

    console.log("After State", this.state);
  };
  handleSubmit = async e => {
    console.log("On Submit", this.state);
    e.preventDefault();
    if (this.handleValidation()) {
      const token = auth.getToken();
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      console.log("Mode", this.props.mode);

      if (this.props.mode == "Add") {
        const { data } = await axios.post(
          `${auth.getURL()}/api/v1/patient`,
          {
            phoneNumber: this.state.phoneNumber,
            firstName: this.state.firstName,
            lastName: this.state.lastName,
            addressSet: this.state.addressSet
          },
          config
        );
      } else {
        const putUrl = `${auth.getURL()}/api/v1/patient/${this.state.id}`;
        const { data } = await axios.put(
          putUrl,
          {
            phoneNumber: this.state.phoneNumber,
            firstName: this.state.firstName,
            lastName: this.state.lastName,
            addressSet: this.state.addressSet
          },
          config
        );
      }
      if (this.props.mode == "Create") {
        this.props.onAdd();
      } else {
        this.props.onEdit();
      }
    }
  };

  render() {
    console.log("After State", this.state);
    return (
      <Container component="main" maxheight="xl" maxWidth="xl">
        <CssBaseline />
        <div>
          <form onSubmit={this.handleSubmit} noValidate>
            <Grid container spacing={3}>
              <Grid item xs={3}>
                <TextField
                  autoComplete="fname"
                  name="firstName"
                  variant="outlined"
                  required
                  fullWidth
                  id="firstName"
                  label="First Name"
                  autoFocus
                  readOnly={this.props.mode == "Edit" ? true : false}
                  value={this.state.firstName}
                  onChange={e => {
                    this.setState({ firstName: e.target.value });
                    let fields = this.state.fields;
                    fields["firstName"] = e.target.value;
                    this.setState({ fields: fields });
                  }}
                />
                <span style={{ color: "red" }}>
                  {this.state.errors["firstName"]}
                </span>
              </Grid>
              <Grid item xs={3}>
                <TextField
                  variant="outlined"
                  required
                  fullWidth
                  id="lastName"
                  label="Last Name"
                  name="lastName"
                  readOnly={this.props.mode == "Edit" ? true : false}
                  onChange={e => {
                    this.setState({ lastName: e.target.value });
                    let fields = this.state.fields;
                    fields["lastName"] = e.target.value;
                    this.setState({ fields: fields });
                  }}
                  value={this.state.lastName}
                  autoComplete="lname"
                />
                <span style={{ color: "red" }}>
                  {this.state.errors["lastName"]}
                </span>
              </Grid>
              <Grid item xs={3}>
                <TextField
                  variant="outlined"
                  required
                  fullWidth
                  id="phoneNumber"
                  label="Phone Number"
                  name="phoneNumber"
                  readOnly={this.props.mode == "Edit" ? true : false}
                  autoComplete="phoneNumber"
                  value={this.state.phoneNumber}
                  onChange={e => {
                    this.setState({ phoneNumber: e.target.value });
                    let fields = this.state.fields;
                    fields["phoneNumber"] = e.target.value;
                    this.setState({ fields: fields });
                  }}
                />
                <span style={{ color: "red" }}>
                  {this.state.errors["phoneNumber"]}
                </span>
              </Grid>

              <Grid item xs={12} spacing={3}>
                <ol
                  style={{
                    paddingInlineStart: "0px"
                  }}
                  id="addressList"
                >
                  {this.state.addressSet.map((address, index) => (
                    <Grid item xs={12} spacing={3}>
                      <ExistingAddressComponent
                        index={index}
                        data={address}
                        mode={this.props.mode}
                        onClick={this.deleteAddress}
                        onUpdate={this.updateAddress}
                      />
                    </Grid>
                  ))}
                </ol>
              </Grid>
              <Grid item xs={12}>
                <AddressComponent onClick={this.addAddress} />
                <span style={{ color: "red" }}>
                  {this.state.errors["Address"]}
                </span>
              </Grid>

              <Grid item xs={12}>
                <div>
                  <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    color="primary"
                  >
                    {this.props.mode} User
                  </Button>
                </div>
              </Grid>
            </Grid>
          </form>
        </div>
      </Container>
    );
  }
}
export default withRouter(User);
